import React from 'react';
import { useNutrientStatusValue } from '../hooks/useNutrientStatusValue';
import { DataSectionValue } from '../compontents/DataSectionValue';

export const MacroDataSection = ({
  title,
  name,
  subSection,
  calculateValue,
  functionState,
  isMenu,
}) => {
  const {
    renderProteinStatus,
    renderNutrientStatus,
  } = useNutrientStatusValue();

  const renderMacroStatus = () => {
    if (name === 'protein')
      return renderNutrientStatus(
        0.8,
        2,
        calculateValue(functionState, name, 'macro')
      );
    if (name === 'carbohydrate')
      return renderNutrientStatus(
        2,
        3.8,
        calculateValue(functionState, name, 'macro')
      );
    if (name === 'totalFat')
      return renderNutrientStatus(
        0.8,
        1.2,
        calculateValue(functionState, name, 'macro')
      );
  };

  return (
    <div className='data-section'>
      <h3 className='data-section__title'>{title}</h3>
      <div className='data-section__content'>
        <DataSectionValue
          name={name}
          title='Celkem'
          calculateValue={calculateValue}
          functionState={functionState}
          nutritionType='macro'
          renderStatus={renderMacroStatus}
          isMenu={isMenu}
        />
        {subSection.map(({ title, name }) => (
          <DataSectionValue
            key={title}
            name={name}
            title={title}
            calculateValue={calculateValue}
            functionState={functionState}
            nutritionType='macro'
            isMenu={isMenu}
          />
        ))}
      </div>
    </div>
  );
};
