import { useUserBiometrics } from '../hooks/useUserBiometrics';

export const useNutrientStatusValue = () => {
  const { biometrics, getBMRValue, getTEEValue } = useUserBiometrics();

  const { weight } = biometrics;

  const renderCaloricStatus = (currentValue) => {
    if (currentValue > getTEEValue())
      return 'data-section__value--error data-section__value--extensive';
    if (currentValue < getBMRValue())
      return 'data-section__value--error data-section__value--deficite';
    return 'data-section__value--success';
  };

  // const renderProteinStatus = (currentValue) => {
  //   if (currentValue > weight.value * 2)
  //     return 'data-section__value--error data-section__value--extensive';
  //   if (currentValue < weight.value * 0.8)
  //     return 'data-section__value--error data-section__value--deficite';
  //   return 'data-section__value--success';
  // };

  const renderNutrientStatus = (min, max, currentValue) => {
    //!check if correct for carbs
    console.log(weight.value * 2, weight.value * 3.8);

    if (currentValue > weight.value * max)
      return 'data-section__value--error data-section__value--extensive';
    if (currentValue < weight.value * min)
      return 'data-section__value--error data-section__value--deficite';
    return 'data-section__value--success';
  };

  return { renderCaloricStatus, renderNutrientStatus };
};
