import React from 'react';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';

export const DataSectionValue = ({
  name,
  title,
  calculateValue,
  functionState,
  nutritionType,
  renderStatus,
  isMenu,
}) => {
  return (
    <p className='data-section__data'>
      {title}:
      <span
        className={`data-section__value ${
          renderStatus && isMenu && renderStatus()
        }`}
      >
        {calculateValue(functionState, name, nutritionType)} g
        <ArrowDownwardIcon className='data-section__down' />
        <ArrowUpwardIcon className='data-section__up' />
      </span>
    </p>
  );
};
